select,
textarea,
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[name="value"] {
  appearance: none;

  @extend .form-control;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  appearance: none;
  margin: 0;
}

button,
input[type="submit"] {
  @extend .btn;
  @extend .btn-primary;
}

input[type=number] {
  -moz-appearance:textfield;
}

label {
  color: #000;
}

// custom checkbox
.custom-control {
  position: relative;
  display: inline;
  padding-left: 30px;
  cursor: pointer;
  display: inline-block;
}

.custom-control-input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.custom-control-indicator {
  display: block;
  width: 16px;
  height: 16px;
  pointer-events: none;
  user-select: none;
  border-radius: 3px;
  border: 2px solid $border-solid-color;
  background-color: white;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  transform: translateY(-50%);

  @include absolute(50%,null,null,0);
}

.custom-control-input:checked~.custom-control-indicator {
  color: #fff;
  background-color: $primary-color;
  border-color: $primary-color;
}

.custom-checkbox .custom-control-input:checked~.custom-control-indicator {
  background-image: url(data:image/svg+xml;base64,);
}

.custom-checkbox--radius {
  padding-left: 25px;

  .custom-control-indicator {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    border: none;
    background-color: #efefef;
  }
}

.custom-control--lg .custom-control-indicator {
  width: 20px;
  height: 20px;
}

.custom-control--border .custom-control-indicator {
  border: 1px solid #ccc;
  background-color: #fff;
}

.custom-control--border-x2 .custom-control-indicator {
  border-width: 2px;
}

// custom radio
.custom-radio {
  padding-left: 35px;

  .custom-control-indicator {
    width: 25px;
    height: 25px;
  }
}

.custom-radio .custom-control-input:checked~.custom-control-indicator {
  background-image: url(data:image/svg+xml;base64,);
}

.custom-radio--radius .custom-control-indicator {
  border-radius: 50%;
}
