// POLICY
.maxus-policy {
}

.maxus-policy__item {
  text-align: center;
  padding: 20px 25px;

  @include media(">=md") {
    padding: 0 25px;
  }

  &:hover,
  &.maxus-policy__item--hover {
    background: url('data:image/png;base64,') no-repeat center top;
    color: #000;

    @include media(">=md","<1300px") {
      background-size: contain;
    }

    .maxus-policy__icon {
      color: #000;
    }
  }

  a {
    color: $text-color;
  }
}

.maxus-policy__icon {
  font-size: 48px;
  margin-bottom: 0;

  @include media(">=md") {
    padding-top: 72px;
  }
}

.maxus-policy__name {
  font-size: 24px;
}

.maxus-policy--small-style {
  padding: 20px 0 21px;
  margin: 0 -10px;
  border-top: 1px solid #e1e1e1;

  @include media(">=md") {
    border-top: none;
    margin: 0;
  }

  .maxus-policy__item {
    float: left;
    width: 50%;
    padding: 0;

    @include media(">=ss") {
      width: 25%;
    }

    &:hover {
      background: none;

      a,
      .maxus-policy__icon {
        color: $primary-color;
      }
    }
  }

  .maxus-policy__icon {
    font-size: 30px;
    padding: 0;
  }

  .maxus-policy__name {
    font-size: 12px;
  }
}

.maxus-policy--border {
  @include media(">=md") {
    border-left: 1px solid #e1e1e1;
  }
}

.maxus-icon {
  display: inline-block;
  padding: 0 20px;

  @include media('>=md') {
    padding: 0 10px;
    width: 20%;
  }

  @include media('>=1300px') {
    width: 16.66667%;
  }

  & + .maxus-icon {
    margin-left: -5px;
    border-left: 1px solid #d7d7d7;
  }

  .maxus {
    font-size: 60px;
    line-height: 1;
    display: block;
    margin-bottom: 6px;
    color: $secondary-color;
    transition: all .3s ease;

    &:hover,
    &:focus {
      color: $third-color;
    }
  }
}

.maxus-icon-wrapper {
  overflow: auto;
  white-space: nowrap;
}
